.book-online-button {
  position: relative;
  transition: all 0.3s ease-in-out;
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.2);
  background: rgb(181, 143, 94);
  border-radius: 10%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #ffff;
  gap: 10px;
  font-weight: bold;
  border: 3px solid #ffffff4d;
  outline: none;
  overflow: hidden;
}

.book-online-button__icon {
  width: 24px;
  height: 24px;
  transition: all 0.3s ease-in-out;
}

.book-online-button:hover {
  transform: scale(1.05);
  border-color: #fff9;
}

.book-online-button:hover .icon {
  transform: translate(4px);
}

.book-online-button:hover::before {
  animation: shine 1.5s ease-out infinite;
}

.book-online-button::before {
  content: "";
  position: absolute;
  width: 100px;
  height: 100%;
  background-image: linear-gradient(
    120deg,
    rgba(255, 255, 255, 0) 30%,
    rgba(255, 255, 255, 0.8),
    rgba(255, 255, 255, 0) 70%
  );
  top: 0;
  left: -100px;
  opacity: 0.6;
}

@keyframes shine {
  0% {
    left: -100px;
  }

  60% {
    left: 100%;
  }

  to {
    left: 100%;
  }
}
