.react-select__container {
  background: transparent;
  border: 1px black solid;
}

.react-select__control {
  background: transparent;
  border-radius: 0;
}

.css-13cymwt-control {
  background: transparent;
  border-radius: 0;
}

.react-select__value-container {
  font-size: large;
}

.react-select-control {
  border-radius: 0;
  background: transparent;
}

.form__submit {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 40px;
  padding: 0;
  gap: 3px;
  width: 140px;
  background-color: rgb(181, 143, 94);
  outline: 3px rgb(181, 143, 94) solid;
  outline-offset: -3px;
  border-radius: 30px;
  border: none;
  cursor: pointer;
  transition: 400ms;
}

.form__submit-svg {
  height: 30px;
}

.form__submit-text {
  margin: 0;
  font-size: 20px;
}

.form__submit .form__submit-text {
  color: white;
  font-weight: 700;
  transition: 400ms;
}

.form__submit .form__submit-svg path {
  transition: 400ms;
}

.form__submit:hover {
  background-color: transparent;
}

.form__submit:hover .form__submit-text {
  color: rgb(181, 143, 94);
}

.form__submit:hover .form__submit-svg path {
  fill: rgb(181, 143, 94);
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
textarea:-webkit-autofill,
textarea:-webkit-autofill:hover,
textarea:-webkit-autofill:focus,
select:-webkit-autofill,
select:-webkit-autofill:hover,
select:-webkit-autofill:focus {
  -webkit-text-fill-color: black;
  -webkit-box-shadow: 0 0 0px 1000px #f5f0f0 inset;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type="number"] {
  -moz-appearance: textfield;
}

@media screen and (max-width: 500px) {
  .contact-form__header {
    font-size: 50px;
  }
}
